.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: calc(
      100vh - 70px
    ); // We take 100vh (size of screen) - 70px (size of navbar) to prevent overflow
    background-color: lightsalmon;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;

    // Remove scrollbar
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}
